const getFieldValue = (item, fieldName) => {
  return (
    (item &&
      item.field &&
      item.field[fieldName] &&
      item.field[fieldName].value) ||
    ''
  );
};
export const preProcessData = (ticket, stripColor, backgroundColor) => {
  const editorialGridTabs = [];
  ticket &&
    ticket.length > 0 &&
    ticket.forEach(item => {
      editorialGridTabs.push({
        fields: {
          stripColor: {
            fields: {
              colorCode: {
                value: stripColor || 'blue',
              },
            },
          },
          backgroundColor: {
            fields: {
              colorCode: {
                value: backgroundColor || 'LightGrey',
              },
            },
          },
          title: {
            value: getFieldValue(item, 'ticketTitle'),
          },
          imageTextLink: {
            value: {
              href: getFieldValue(item, 'ticketWebUrl'),
              linktype: 'internal',
              target: '_blank',
              class: '',
            },
          },
          subTitle: {
            value:
              getFieldValue(item, 'ticketCurrency') +
              ' ' +
              getFieldValue(item, 'ticketPrice'),
          },
        },
      });
    });
  return editorialGridTabs;
};
