import React from 'react';
import { withNamespaces } from 'react-i18next';
import { withSitecoreContext } from '@sitecore-jss/sitecore-jss-react';

import CategoryCardComponent from '../../../components/organism/CategoryCardComponent/';
import Box from '../../../components/molecules/Box';
import useStyles from './style';
import { preProcessData } from './preprocessData';
import withErrorBoundary from '../../../utils/HOCs/withErrorBoundary';
import { COMPONENT_NAME } from './locators';
import { getPageName, getComponentName } from '../../../utils/getPageComponentInfo';
import { TICKET_COMPONENT_ID } from '../../../../constants/index';

const Tickets = props => {
  const { fields, params } = props;
  const { ticket } = fields || '';
  const { ticketHeading, ticketStripColor, ticketBackgroundColor } = params || '';
  const classes = useStyles();
  const editorialGridTabs = preProcessData(
    ticket,
    ticketStripColor,
    ticketBackgroundColor
  );
  return (
    ticket &&
      ticket.length > 0 ? (
        <Box className={classes.EditorialGridWrapper} id={TICKET_COMPONENT_ID} data-locators={COMPONENT_NAME}>
          <CategoryCardComponent
            tileData={editorialGridTabs}
            segmentHeading={{ value: ticketHeading }}
            isPriceCard={true}
            pageName={getPageName(props)}
            componentName={getComponentName(props)}
          />
        </Box>
      ) : null
  );
};

export default withErrorBoundary(
  withSitecoreContext()(withNamespaces()(Tickets))
);
